/* FOOTER */

.footer {
  left: 50%;
  transform: translateX(-50%);
  position: fixed;
  bottom: 0;
  background: #16141a;
  background: var(--theme-bg);
  text-align: center;
  z-index: 8;

  &__text {
    color: #ecdede;
    color: var(--theme-primary);
    font-size: 20px;
    display: inline;
    margin: 0;
  }
}

/* FOOTER MEDIA */

@media screen and (min-width: 576px) {
  .footer {
    font-size: 1em;
  }
}
